@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DynaPuff&display=swap');
.App{
  text-align: center;
  overflow-x: hidden;
}
body{
  overflow-x: hidden;
}
.nav h1{
  font-size: 40px;
  color: white;
  top: -2.3vh;
  text-align: left;
  position: relative;
  left: 2vw;
}
#Preview{
  color: white;
  font-size: 225px;
  position: relative;
  top: 0;
}
#Preview-2{
  color: white;
  letter-spacing: 5px;
  font-size: 75px;
  position: relative;
  top: -22%;
}
#my-biografy{
  color: white;
  position: relative;
  left: 0;
  font-size: 25px;
}
.nav{
  background: black;
  width: 100%;
  position: relative;
  border-bottom: 1px solid white;
  height: 75px;
  overflow: hidden;
}

.nav a{
  padding-left: 30px;
  font-size: 3vmin;
  color: white;
  top: -105%;
  position: relative;
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
}
.img-logo{
  width: 2.5vw;
  height: 5vh;
  top:1.5vh;
  left: 10.5vw;
  z-index: 2;
  position: absolute;
  animation: anim-logo ease infinite 3s;
}
@keyframes anim-logo {
  0% {
    transform: rotate(0deg);
  }
  50%{
    transform: rotate(360deg);
  }
  100%{
    transform: rotate(0deg);
  }


}
.main{
  padding: 0;
  margin: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  /*background: radial-gradient(circle, rgba(0,0,0,1) 23%, #cc7622 52%);*/
  /*background: radial-gradient(rgba(180, 119, 49, 1) 0%, rgba(12, 12, 12, 1) 50%);*/
  /*background-image: url("Assets/fon_pyatna_temnyy_91678_2560x1440.jpg");*/
  background: linear-gradient(45deg, #fed90e, #ff0066);
  border-bottom: 1px solid  white;
}

#Preview-3{
  color: white;
  position: relative;
  top: 65vh;
  margin: 10px;
  font-size: 50px;
  line-height: 40px;
  z-index: 3;
}
span{
  color: orange;
}
.footer{
  background: #212121;
  border-top:1px solid white ;
  width: 100%;
  height: 25%;
  top:500%;
  position: absolute;
}
#Preview-4{
  color: white;
  top: 65vh;
  position: relative;
}
.AboutMe-border{
  background: #0f0f0f;
  width: 90%;
  border-radius: 20px;
  top: 165%;
  position: absolute;
  left: 5%;
  height: 70%;
  border: 2px solid white;
  box-shadow: 0 0 10px white;
  z-index: 1;
}
.AboutMe-border h1{
  color: orange;
  position: relative;
  left: 0;
}
.AboutMe-border img {
  width: 22%;
  height: 70%;
  left: -35%;
  border-radius: 20px;
  top: 5%;
  position: relative;
}
#purple{
  color: #ff0051;
}
.AboutMe-border #AboutMe-text{
  color: white;
  padding: 10px;
  position: relative;
}
.panels{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  height: 400px;
  grid-column-gap: 50px;
  top: 230%;
  width: 90%;
  left: 5%;
  position: absolute;
}
.panel-1{
  border-radius: 20px;
  background: #0e0e0e;
  box-shadow: 0 0 10px white;
  border: 2px solid white;
}
.panel-2{
  border-radius: 20px;
  background: #0e0e0e;
  box-shadow: 0 0 10px white;
  border: 2px solid white;
}
.panel-3{
  border-radius: 20px;
  background: #0e0e0e;
  box-shadow: 0 0 10px white;
  border: 2px solid white;
}
.nav h1:hover{
  background: linear-gradient(45deg, #ff0066 0%, #fed90e 10%);
  color: white;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}
.panels .panel-1 img{
  width: 300px;
  height: 300px;
  opacity: 1;
  left: 0%;
  top: 10%;
  position: relative;
}
.panels .panel-1 h1{
  background: linear-gradient(#ff0066, #fed90e);
  color: white;
  font-family: 'Roboto', sans-serif;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  font-size: 50px;
  position: relative;
  top: 5%;
}
.panels .panel-2 img{
  width: 250px;
  height: 250px;
  opacity: 1;
  left: 0;
  top: 17%;
  position: relative;
}
.panels .panel-2 h1{
  background: linear-gradient(#ff0066, #fed90e);
  color: white;
  font-family: 'Roboto', sans-serif;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  font-size: 50px;
  position: relative;
  top: 15%;
}
.panels .panel-3 img{
  width: 330px;
  height: 330px;
  opacity: 1;
  left: 0;
  top: 10%;
  position: relative;
}
.panels .panel-3 h1{
  background: linear-gradient(#ff0066, #fed90e);
  color: white;
  font-family: 'Roboto', sans-serif;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  font-size: 50px;
  position: relative;
  top: -2%;
}
