    display: grid;
    grid-template-columns: 1fr;
    top: 298%;
    height: 1200px;
    grid-row-gap: 30px;
  }
  .panels .panel-2 img{
    top: 10%;
  }
  .panels .panel-2 h1{
    top: 5%;
  }
  .panels .panel-3 img{
    top: 3%;
  }
  .panels .panel-3 h1{
    font-size: 45px;
  }
  .img-logo{
    width: 30px;
    top: 20px;
